import React from "react";
import { a, SpringValue, to, useSpring, useTrail } from "react-spring";
// Styles
import styled from "styled-components";

const Container = styled(a.aside)`
  background-color: #000;
  color: #fff;
  font-family: cornerstone;
  font-size: 2rem;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  & a {
    color: #fff;
  }
`;
const CloseButton = styled(a.div).attrs({
  role: "button"
})`
  padding: 1rem;
  transform-origin: 50% 50%;
  width: auto;

  &:hover {
    cursor: pointer;
  }

  & i {
    color: inherit;
    font-size: 1.5rem;
  }
`;
const OfferContainer = styled.div`
  width: 100%;
  text-align: center;
`;
const OptionsContainer = styled.div`
  display: flex;
  justify-content: space-evenly;
  margin: 32px 0 0;
`;
const OptionBLM = styled.a`
  font-weight: bold;

  &:after {
    background-image: linear-gradient(
      180deg,
      transparent,
      transparent 6.25%,
      #fce21b 0,
      #fce21b 25%,
      transparent 0,
      transparent 40.625%,
      #fce21b 0,
      #fce21b 59.375%,
      transparent 0,
      transparent 75%,
      #fce21b 0,
      #fce21b 93.75%,
      transparent 0,
      transparent
    );
    content: "";
    height: 0.75em;
    left: 0;
    display: inline-block;
    position: relative;
    right: 0;
    width: 100%;
  }
  &:hover {
    text-decoration: none;
  }
`;
const TextContainer = styled.div`
  font-size: 6rem;
  font-weight: bold;
  line-height: 1;
`;

const bannerText = ["BLACK", "LIVES", "MATTER"];

type TransitionProps = {
  opacity: SpringValue<number>;
  height: SpringValue<string>;
};

type BannerProps = {
  show: boolean;
  toggleBanner: Function;
  transition: TransitionProps;
};

function Banner({ show, toggleBanner, transition }: BannerProps): JSX.Element {
  // BEGIN:: Button
  const [{ color, z }, set] = useSpring(() => ({
    config: { friction: 13 },
    color: "#8be9fd",
    z: 0
  }));
  // END:: Button
  // BEGIN:: text trail
  const textTrail = useTrail(bannerText.length, {
    opacity: show ? 1 : 0,
    x: 0,
    height: 80,
    from: { opacity: 0, x: 40, height: 0 }
  });
  // END:: text trail

  return (
    <Container style={transition as any}>
      <TextContainer>
        {textTrail.map(({ x, height, ...rest }, index) => (
          <a.div
            key={index}
            className="trails-text"
            style={
              {
                ...rest,
                transform: to([x], x => `translate3d(0,${x}px,0)`)
              } as any
            }
          >
            <a.div style={{ height }}>{bannerText[index]}</a.div>
          </a.div>
        ))}
      </TextContainer>
      <OfferContainer>
        Please consider donating to:
        <OptionsContainer>
          <OptionBLM href="https://blacklivesmatter.com/">
            BLACKLIVESMATTER
          </OptionBLM>
        </OptionsContainer>
      </OfferContainer>
      <CloseButton
        aria-label="Close banner"
        onClick={(): void => toggleBanner()}
        onMouseEnter={(): any => set({ color: "#50fa7b", z: 180 })}
        onMouseLeave={(): any => set({ color: "#8be9fd", z: 0 })}
        style={
          {
            color,
            opacity: transition.opacity
          } as any
        }
      >
        <a.i
          className="fas fa-times"
          style={{
            transform: to([z], z => `rotate(${z}deg)`)
          }}
        ></a.i>
      </CloseButton>
    </Container>
  );
}

export default Banner;
